import { DeepPartial, LocalesMock } from '../core'
import { Campaign, CampaignRule } from './campaign.model'

export function CampaignMock(options?: DeepPartial<Campaign>): Campaign {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'CAMPAIGN_ID',
    code: options?.code || 'CAMPAIGN_CODE',

    name: LocalesMock(options?.name),
    description: LocalesMock(options?.description),
    rules: options?.rules?.map((r) => CampaignRuleMock(r)),

    startDate: options?.startDate || now,
    endDate: options?.endDate || now,

    createdAt: options?.createdAt || now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: options?.updatedAt || now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}

export function CampaignRuleMock(
  options?: DeepPartial<CampaignRule>
): CampaignRule {
  return {
    name: LocalesMock(options?.name),
    description: options?.description
      ? LocalesMock(options?.description)
      : undefined,
    trigger: options?.trigger || 'DEPOSIT',
    deposit: {
      type: options?.deposit?.type || 'BALANCE',
      value: options?.deposit?.value || 1,
    },
    reward: {
      type: options?.reward?.type || 'AMOUNT',
      value: options?.reward?.value || 1,
    },
    frequency: {
      unit: options?.frequency?.unit || 'days',
      value: options?.frequency?.value || 1,
    },
  }
}
